import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Grid,
  Box,
} from "@material-ui/core";
import React, { useState } from "react";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import TextInput from "../../../../../../components/input/TextInput";
import {
  ArrowDownDoubleIcon,
  ArrowUpDoubleIcon,
  BriefcaseIcon,
  CardIcon,
  CloseIcon,
  LocationIcon,
  PhoneIcon,
  UserIcon,
} from "../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { CustomerResponse } from "../../../../../../external-services/api/models/CustomerModels";
import colors from "../../../../../../theme/statics/colors";
import useStyles from "./PopupCustomer.style";

interface PopupCustomerProps {
  open: boolean;
  customer: CustomerResponse;
  handleClose: () => void;
  isCreate?: boolean;
}

const PopupCustomer = (props: PopupCustomerProps) => {
  const [isShowAdvance, setIsShowAdvance] = useState<boolean>(false);
  const ShowMoreIcon = () => {
    return (
      <ArrowDownDoubleIcon
        htmlColor={colors.primary[100]}
        style={{ width: 20, height: 20, marginLeft: 5 }}
      />
    );
  };
  const ShowLessIcon = () => {
    return (
      <ArrowUpDoubleIcon
        htmlColor={colors.primary[100]}
        style={{ width: 20, height: 20, marginLeft: 5 }}
      />
    );
  };

  const { open, handleClose, isCreate } = props;
  const classes = useStyles();
  const handleShowAdvance = () => {
    setIsShowAdvance(!isShowAdvance);
  };
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      fullWidth
      maxWidth="sm"
      classes={{
        paper: classes.paper,
      }}
    >
      <DialogTitle disableTypography className={classes.dialogTitle}>
        <PosTypography size={TEXTSIZE.size20} fontWeight="medium">
          {isCreate ? "Thêm mới khách hàng" : "Thông tin khách hàng"}
        </PosTypography>
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={handleClose}
        >
          <CloseIcon htmlColor={colors.icon.secondary} />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.contentWrap}>
        <Grid container style={{ gap: "0 0" }}>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <UserIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Tên khách hàng"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <PhoneIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Số điện thoại"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <CardIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Mã khách hàng"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <BriefcaseIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Nhân viên phụ trách"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={12} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <LocationIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Địa chỉ"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <LocationIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Khu vực"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          <Grid item xs={6} className={classes.contentItem}>
            <TextInput
              labelIcon={
                <LocationIcon
                  style={{ width: 20, height: 20 }}
                  htmlColor={colors.icon.secondary}
                />
              }
              textLabel="Phường xã"
              labelColor={colors.text[70]}
              lablelTextSize={TEXTSIZE.size14}
              fullWidth
              width={"100%"}
              variant={"outlined"}
            />
          </Grid>
          {isShowAdvance && (
            <>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Vẽ"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Đi"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Rồi"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Anh"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
              <Grid item xs={6} className={classes.contentItem}>
                <TextInput
                  labelIcon={
                    <UserIcon
                      style={{ width: 20, height: 20 }}
                      htmlColor={colors.icon.secondary}
                    />
                  }
                  textLabel="Làm"
                  labelColor={colors.text[70]}
                  lablelTextSize={TEXTSIZE.size14}
                  fullWidth
                  width={"100%"}
                  variant={"outlined"}
                />
              </Grid>
            </>
          )}
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogFooter}>
        <Box>
          {!isShowAdvance ? (
            <PosButton
              style={{ padding: 0 }}
              customSize="small"
              customType="text"
              onClick={handleShowAdvance}
              color="primary"
              iconElm={ShowMoreIcon}
              iconPosition="right"
            >
              Mở rộng
            </PosButton>
          ) : (
            <PosButton
              style={{ padding: 0 }}
              customSize="small"
              customType="text"
              onClick={handleShowAdvance}
              color="primary"
              iconElm={ShowLessIcon}
              iconPosition="right"
            >
              Khum mở rộng nữa
            </PosButton>
          )}
        </Box>
        <Box display={"flex"}>
          <Box marginRight={"12px"}>
            <PosButton
              customSize="small"
              customType="outlined"
              onClick={handleClose}
              color="primary"
            >
              Thoát
            </PosButton>
          </Box>
          <Box>
            <PosButton customSize="small" onClick={handleClose} color="primary">
              {isCreate ? "Thêm" : "Cập nhật"}
            </PosButton>
          </Box>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default PopupCustomer;
